/* main.css — migrated from utils-site.html <style> block */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary: #667eea;
    --secondary: #764ba2;
    --accent: #f093fb;
    --dark: #1a1a2e;
    --light: #eee;
    --success: #2ecc71;
    --warning: #f39c12;
    --danger: #e74c3c;
    --info: #3498db;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    line-height: 1.6;
    color: #333;
    background: linear-gradient(135deg, var(--primary) 0%, var(--secondary) 100%);
    min-height: 100vh;
}

.container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 20px;
}

.header {
    text-align: center;
    margin-bottom: 50px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
    border-radius: 20px;
    padding: 40px;
    color: white;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

.header h1 {
    font-size: 3rem;
    margin-bottom: 15px;
    font-weight: 700;
    background: linear-gradient(45deg, #fff, #f093fb);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.header p {
    font-size: 1.2rem;
    opacity: 0.9;
    max-width: 600px;
    margin: 0 auto;
}

.search-bar {
    margin: 30px 0;
    position: relative;
}

.search-input {
    width: 100%;
    padding: 15px 50px 15px 20px;
    font-size: 1.1rem;
    border: none;
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    color: white;
    placeholder-color: rgba(255, 255, 255, 0.7);
}

.search-input::placeholder {
    color: rgba(255, 255, 255, 0.7);
}

.search-icon {
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    color: rgba(255, 255, 255, 0.7);
}

.categories {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 40px;
    flex-wrap: wrap;
}

.category-btn {
    padding: 10px 20px;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    border: none;
    border-radius: 25px;
    color: white;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 0.9rem;
}

.category-btn:hover,
.category-btn.active {
    background: rgba(255, 255, 255, 0.3);
    transform: translateY(-2px);
}

.utils-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 25px;
    margin-bottom: 50px;
}

.util-card {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    border-radius: 20px;
    padding: 30px;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
    cursor: pointer;
    border: 1px solid rgba(255, 255, 255, 0.2);
    position: relative;
    overflow: hidden;
}

.util-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: var(--gradient);
}

.util-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.util-card.git::before { background: linear-gradient(90deg, #f14e32, #ff6b35); }
.util-card.docker::before { background: linear-gradient(90deg, #0db7ed, #2496ed); }
.util-card.python::before { background: linear-gradient(90deg, #3776ab, #ffd343); }
.util-card.javascript::before { background: linear-gradient(90deg, #f7df1e, #323330); }
.util-card.css::before { background: linear-gradient(90deg, #1572b6, #33a9dc); }
.util-card.linux::before { background: linear-gradient(90deg, #fcc624, #000); }
.util-card.regex::before { background: linear-gradient(90deg, #e74c3c, #c0392b); }
.util-card.database::before { background: linear-gradient(90deg, #336791, #4479a1); }
.util-card.api::before { background: linear-gradient(90deg, #ff6b35, #f7931e); }
.util-card.security::before { background: linear-gradient(90deg, #2ecc71, #27ae60); }
.util-card.devops::before { background: linear-gradient(90deg, #9b59b6, #8e44ad); }
.util-card.react::before { background: linear-gradient(90deg, #61dafb, #21232a); }

.util-icon {
    font-size: 3rem;
    margin-bottom: 15px;
    opacity: 0.8;
}

.util-card h3 {
    font-size: 1.5rem;
    margin-bottom: 15px;
    color: #2c3e50;
}

.util-card p {
    color: #7f8c8d;
    margin-bottom: 20px;
    line-height: 1.6;
}

.util-features {
    list-style: none;
    margin-bottom: 20px;
}

.util-features li {
    padding: 5px 0;
    color: #34495e;
    position: relative;
    padding-left: 20px;
}

.util-features li::before {
    content: '✓';
    position: absolute;
    left: 0;
    color: var(--success);
    font-weight: bold;
}

.util-tags {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
}

.tag {
    background: rgba(102, 126, 234, 0.1);
    color: var(--primary);
    padding: 4px 12px;
    border-radius: 15px;
    font-size: 0.8rem;
    font-weight: 500;
}

.page-content {
    display: none;
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    border-radius: 20px;
    padding: 40px;
    box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
    margin-top: 20px;
}

.page-content.active {
    display: block;
}

.back-btn {
    background: var(--primary);
    color: white;
    border: none;
    padding: 12px 24px;
    border-radius: 10px;
    cursor: pointer;
    margin-bottom: 30px;
    font-size: 1rem;
    transition: all 0.3s ease;
}

.back-btn:hover {
    background: var(--secondary);
    transform: translateX(-5px);
}

.command-section {
    background: #1e1e1e;
    color: #d4d4d4;
    padding: 25px;
    border-radius: 15px;
    margin: 20px 0;
    font-family: 'Monaco', 'Menlo', monospace;
    overflow-x: auto;
    position: relative;
}

.command-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, #ff6b6b, #4ecdc4, #45b7d1);
}

.command-section h4 {
    color: #4ec9b0;
    margin-bottom: 15px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
}

.footer {
    text-align: center;
    margin-top: 50px;
    padding: 30px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
    border-radius: 20px;
    color: white;
}

@media (max-width: 768px) {
    .utils-grid {
        grid-template-columns: 1fr;
    }
    
    .header h1 {
        font-size: 2rem;
    }
    
    .categories {
        justify-content: flex-start;
        overflow-x: auto;
        padding-bottom: 10px;
    }
    
    .category-btn {
        flex-shrink: 0;
    }
}

.hidden {
    display: none;
}
